import {useData, useState, useMounted, useEffect, useComputed, useUpdated} from 'vue-hooks';
import io from 'socket.io-client';
let socket;
export function loginHook(value){

//const ENDPOINT = 'http://localhost:5000/';

const ENDPOINT = 'https://fast-citadel-36819.herokuapp.com/';
const data = useData({isShowing:value});
 
 
const [message, setMessage] = useState('');
const [messages, setMessages] = useState([]);
const [activeUsers, setactiveUsers] = useState([]);
const room = 'app';
 var browserBackRefreshCloseMessage = "browser action";
var keyBoardRefreshMessage = "refresh button is pressed keyboard";
var isReconnect = false;
 

useEffect(()=> {
    
    socket = io(ENDPOINT);
    document.title = 'You are: ' +  data.isShowing; 
    
    const name = data.isShowing;

     socket.emit('join', {name, room}, ()=>{
	 
	 	
	 });
	
  },[ENDPOINT]);

useEffect(()=> {
  		history.pushState(null, document.title, location.href);
		window.addEventListener('popstate', function (event){
  			history.pushState(null, document.title, location.href);
 					});
		 
		//this.$once("hook:beforeDestroy", () => {
      		//	window.removeEventListener("beforeunload", this.preventNav);
    		//})	
 

  },[ENDPOINT]);
//return () => {
//		socket.emit('disconnect');
//		socket.off();
//	}

useEffect(()=> {

	socket.on('message', (message)=>{
		setMessages([...messages, message]);
	        
		 document.title = message.text;	
	})
	

}, [messages]);

useEffect(()=> {

	socket.on('users', (users)=>{
		  
		 var activeUse = [];
		 activeUse=Object.values(users)[0].split(',');
		 setactiveUsers(activeUse);
				 
		})
	 
	 },[activeUsers]);

const sendMessage = (event) => {
	event.preventDefault();
	 
	if(message){
            socket.emit('sendMessage', message, () => {setMessage('')});
	}
}

const logOff = () => {
	 
	console.log("disconnect");
	socket.emit('disconnectLog', 'disconnect user');
	socket.off();	 
}


console.log(message,messages);
return{
	data,
	message,
	messages,
	activeUsers,
	setMessage,
	setMessages,
	sendMessage,
	logOff
	 
	 
}
}
